import React, { useState } from "react";
import { TextInput, Box } from "grommet";
import { Send } from "grommet-icons";

const Component = ({ onSubmit, placeholder, value = "" }) => {
  const [textInputValue, setTextInputValue] = useState(value);
  return (
    <Box border="1px solid green" direction="row">
      <TextInput
        placeholder={placeholder}
        reverse={true}
        value={textInputValue}
        onKeyPress={(event) => {
          if (event.key === "Enter") {
            onSubmit && onSubmit(textInputValue);
            setTextInputValue("");
          }
        }}
        plain
        onChange={(event) => {
          setTextInputValue(event.target.value);
        }}
      />
      <Box
        onClick={() => {
          onSubmit && onSubmit(textInputValue);
          setTextInputValue("");
        }}
        pad="small"
        style={{ height: "100%", cursor: "pointer" }}
      >
        <Send style={{ height: "100%", cursor: "pointer" }} />
      </Box>
    </Box>
  );
};

export default Component;
