import React, { useEffect, useState } from "react";
import Chat from "../../components/Chat";
import { Heading } from "grommet";
import { findChatroomMessage, sendChatroomMessage } from "../../library/parse";
import EventObserver from "../EventObserver";

const arrangeId = (arr) => {
  return arr.sort().join("_");
};

const Component = ({
  publicRoom,
  senderRoleName,
  recipientRoleName,
  chatroomName,
  placeholder,
}) => {
  const chatroomId = publicRoom
    ? recipientRoleName
    : arrangeId([senderRoleName, recipientRoleName]);
  const [messages, setMessages] = useState([]);
  const [createEvent, setCreateEvent] = useState(null);

  const modelToMessage = (models) => {
    return models.map((c) => {
      return {
        message: c.message,
        senderName: c.name,
        isThirdPerson: c.role !== senderRoleName,
      };
    });
  };

  useEffect(() => {
    findChatroomMessage(chatroomId).then((e) => {
      setMessages(modelToMessage(e));
    });
  }, [chatroomId]);

  useEffect(() => {
    if (createEvent) {
      findChatroomMessage(chatroomId).then((e) => {
        setMessages(modelToMessage(e));
      });
    }
  }, [createEvent]);

  return (
    <>
      {chatroomName && <Heading level={3}> {chatroomName} </Heading>}
      <Chat
        textInputPlaceholder={placeholder}
        onSendMessage={(e) => {
          sendChatroomMessage(senderRoleName, publicRoom, e, recipientRoleName);
        }}
        messages={messages}
      />
      <EventObserver
        objectClass={"ChatroomMessage"}
        onEvent={(e, action) => {
          if (action === "create") {
            setCreateEvent(e);
          }
        }}
      />
    </>
  );
};

export default Component;
