import React from "react";
import rtClient from "../library/Realtime";
import Parse from "../library/parse";

class Home extends React.Component {
  componentDidMount() {
    const { onEvent, objectClass } = this.props;
    if (objectClass) {
      var query = new Parse.Query(objectClass);
      query.ascending("createdAt").limit(5);
      const subscription = rtClient.subscribe(query);
      subscription.on("create", (e) => onEvent && onEvent(e, "create"));
      subscription.on("update", (e) => onEvent && onEvent(e, "update"));
      subscription.on("delete", (e) => onEvent && onEvent(e, "delete"));
    }
  }

  componentWillUnmount() {
    rtClient.close();
  }
  render() {
    return <></>;
  }
}

export default Home;
