import React from "react";
import { Box, Text } from "grommet";

const Component = ({ right, message }) => {
  return (
    <Box pad="small" fill={true} direction="row">
      <Box
        overflow="visible"
        flex={{ shrink: 0 }}
        fill={false}
        round="8px"
        pad="small"
        border="4px"
        background="brand"
        margin={{ left: right ? "auto" : "0" }}
      >
        <Text>{message.senderName}</Text>
        <br />
        {message.message}
        {message.isThirdPerson}
      </Box>
    </Box>
  );
};

export default Component;
